@import "./mixins";

:root {
  //TODO----------------- ЦВЕТА ------------------------------------------------------------------------
  --color-black: #000;
  --color-white: #fff;
  --color-eastbay: #484283;
  --color-accent: #f14f4f;
  --color-accent-hover:#cc4949;
  --color-border:rgba(255, 255, 255, 0.5);
  --color-main: #444b58;
  --color-main-hover: #31343c;
  --color-bg: #fff4ee;
  --color-cameo: #dbbba9;
  --color-gray: #b2b5bb;
  --color-line: #808080;
  --color-faq: #242424;
  

   //TODO--------------- ОТСТУПЫ ------------------------------------------------------------------------
  --grid-gap:20px;
  --main-offset: 40px;
  --second-offset: 20px;
  --big-offset: 60px;
  --small-offset: 10px;

  //TODO---------------- ШРИФТЫ ------------------------------------------------------------------------
  --font-family: "Intro", sans-serif;
  --middle-high-font: 700 30px / 140% var(--font-family);
  --middle-font: 400 16px / 140% var(--font-family);
	--small-font: 400 14px / 140% var(--font-family);
	--nav-font: 400 16px / 140% var(--font-family);
	--price-font: 700 20px / 20px var(--font-family);
	--title-font: 400 24px / 24px var(--font-family);
	--team-font: 700 24px / 140% var(--font-family);
}

@include mobile {
	:root {
		--grid-gap: 10px;
		--main-offset: 20px;
		--second-offset: 14px;
		--big-offset: 30px;
		--small-offset: 8px;
	}
}